/* stylelint-disable length-zero-no-unit */
/* stylelint-enable length-zero-no-unit */
.clearfix:after {
  clear: both !important;
  content: "" !important;
  display: block !important;
}

.hidden,
.visually-hidden {
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  overflow: hidden;
  position: absolute !important;
  width: 1px;
}

.margin-control {
  margin: 1.5rem 0;
}

.margin-control-center {
  margin: 1.5rem auto;
}

.margin-control-override {
  margin: 0 !important;
}

:root {
  --system-sans: -apple-system, blinkmacsystemfont, avenir next, avenir,
  segoe ui, helvetica neue, helvetica, ubuntu, roboto, noto, arial, sans-serif;
  --font-jura: "Jura", var(--system-sans);
  --font-roboto: "Roboto", var(--system-sans);
  --color-black: #000;
  --color-black-950: #091830;
  --color-yellow: #F4B400;
  --color-yellow-400: #F8D676;
  --color-blue: #4285F4;
  --color-blue-light: #92B5F3;
  --color-blue-medium: #174EA6;
  --color-blue-950: #091830;
  --color-white: #fff;
  --color-white-800: #E6E0E9;
  --color-green: #58A55D;
  --color-green-light: #91C799;
  --color-green-medium: #35713A;
  --color-grey: #666;
  --color-grey-light: #F2F2F2;
  --color-grey-400: #CCC;
  --color-grey-dark: #666666;
  --color-grey-950: #0F0F0F;
  --color-text: var(--color-blue-950);
  --spacing: 0.8rem;
  --spacing-2: 1.6rem;
  --spacing-3: 2.4rem;
  --spacing-4: 3.2rem;
  --spacing-5: 4.8rem;
  --spacing-6: 6.4rem;
  --wrapper-width: 76.8rem;
  --wrapper-padding: var(--spacing-3);
  --td: .1s;
  --tt: linear;
  --border-radius-sm: .4rem;
  --border-radius-md: .6rem;
  --border-radius: .8rem;
  --border-radius-full: 99rem;
}

:root {
  position: relative;
}

.wrapper-narrow,
.wrapper {
  padding-inline: var(--wrapper-padding);
  width: 100%;
}

.wrapper {
  margin-left: auto;
  margin-right: auto;
  max-width: var(--wrapper-width);
  padding-block: var(--spacing-4);
  min-height: 100vh;
}
.wrapper-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.wrapper-narrow {
  margin-left: auto;
  margin-right: auto;
  max-width: var(--wrapper-width);
  padding-inline: var(--wrapper-padding);
}
.wrapper > .wrapper {
  max-width: none;
  padding: 0;
}

input,
button,
textarea,
select {
  font: inherit;
}

input[type=submit],
input[type=radio],
input[type=checkbox] {
  width: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
}

a {
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}
a:active, a:hover, a:focus {
  text-decoration: underline;
}

img,
svg,
video {
  display: block;
  height: auto;
  max-width: 100%;
}

iframe {
  max-width: 100%;
}

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  word-wrap: normal;
}

:root {
  font-size: 62.5%;
  position: relative;
}

html,
body {
  height: 100%;
  min-height: 100vh;
}

body {
  font-size: 1.6rem;
  line-height: 1.5;
  background-color: var(--color-white);
  background-image: url("../imgs/bg.png");
  background-attachment: fixed;
  background-repeat: repeat;
  font-family: var(--font-roboto);
  margin: 0;
  overflow-x: hidden;
}

tr,
td,
th {
  padding: 10px;
}

strong {
  font-weight: 700;
}

p {
  margin: 0;
}
p.demo {
  font-size: 1.2rem;
  line-height: 5;
  display: block;
  font-weight: 400;
  padding: 0 0 var(--spacing-2);
  text-align: center;
}

.button {
  font-size: 3.2rem;
  line-height: 1.25;
  background: var(--color-blue);
  border-radius: 0.8rem;
  border: 0.3rem solid var(--color-black-950);
  box-shadow: -2px 4px 0px 0px var(--color-black-950);
  color: var(--color-white);
  display: inline-block;
  font-family: var(--font-jura);
  font-weight: 700;
  padding: var(--spacing-2) var(--spacing-4);
  position: relative;
  text-transform: uppercase;
}
.button:is(:hover, :focus):not([disabled]) {
  text-decoration: none;
  box-shadow: -0.1rem 0.2rem 0 0 var(--color-black-950);
  transform: translateY(0.2rem);
}
.button:focus-visible {
  outline: none;
}
.button:is(:active):not([disabled]) {
  box-shadow: 0 0 0 0 var(--color-black-950);
  transform: translateY(0.4rem);
}
.button[disabled], [disabled] .button {
  background: rgba(230, 224, 233, 0.12);
  box-shadow: 0 0 0 0 var(--color-black-950);
  color: rgba(230, 224, 233, 0.38);
}

.guesses {
  align-items: center;
  background: var(--color-blue-medium);
  border-radius: 2rem;
  border: 0.6rem solid var(--color-blue-medium);
  position: relative;
  overflow: hidden;
}
.guesses > form {
  display: block;
  padding: 0;
  inline-size: 100%;
  text-align: center;
}
.guesses__header {
  background: var(--color-blue-medium);
  border-start-end-radius: 0.6rem;
  border-start-start-radius: 0.6rem;
  padding: 2.4rem 1.6rem;
  text-align: center;
}
.guesses__header p {
  font-size: 2.4rem;
  color: var(--color-white);
  font-family: var(--font-jura);
  font-weight: 700;
  letter-spacing: 0.05rem;
}
.guesses__note {
  font-size: 1.2rem;
  line-height: 1.3333333333;
  color: var(--color-white);
  font-weight: 300;
  grid-area: note;
  justify-self: flex-start;
  margin-block-start: 1.2rem;
  padding-inline-start: 2.4rem;
}
.guesses__num {
  font-size: 2.4rem;
  line-height: 1;
  align-items: center;
  background-color: var(--color-black-950);
  block-size: 4.8rem;
  border-radius: var(--border-radius-full);
  color: var(--color-yellow);
  display: flex;
  font-weight: 700;
  inline-size: 4.8rem;
  justify-content: center;
  margin-inline: auto;
}
.guesses__submit {
  grid-area: submit;
}
.guesses__submit .button {
  background: var(--color-yellow);
  border: 0.1rem solid var(--color-blue-medium);
  block-size: 9.6rem;
  color: var(--color-text);
  padding-inline: var(--spacing);
}
.guesses__submit .button[disabled] {
  background: transparent;
  border: 0.1rem solid var(--color-blue-light);
  color: var(--color-blue-light);
}
.guesses__img {
  aspect-ratio: 4/3;
  grid-area: img;
}
.guesses__img img {
  block-size: 100%;
  inline-size: 100%;
  object-fit: cover;
}
.guesses__input {
  background-color: var(--color-blue);
  color: var(--color-black);
  display: grid;
  gap: 1.2rem;
  grid-area: input;
  padding: 2.4rem 1.6rem;
  position: relative;
  text-align: left;
  transition: all var(--td) var(--tt);
  grid-template-areas: "input submit" "note note";
  grid-template-columns: 1fr 4.8rem;
}
.guesses__input button[id=keyboard] {
  background: transparent;
  border-radius: 50%;
  border: 0;
  cursor: pointer;
  position: absolute;
  right: 1.6rem;
  top: 50%;
  transform: translateY(-50%);
}
.guesses__input button[id=keyboard] img {
  pointer-events: none;
}
.guesses__input-grp {
  background-color: var(--color-white);
  border-radius: var(--border-radius-sm);
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  padding: var(--spacing-2) var(--spacing-4) var(--spacing-4);
  position: relative;
  overflow: hidden;
  grid-area: input;
}
.guesses__input-grp:after {
  background: var(--color-blue-medium);
  block-size: 0.5rem;
  bottom: 0;
  content: "";
  inline-size: 100%;
  left: 0;
  position: absolute;
  transform: translateY(0);
  transition: all var(--td) var(--tt);
}
.guesses__input input[type=text] {
  font-size: 1.9rem;
  background: transparent;
  border-bottom: 1px solid var(--color-yellow);
  border: 0;
  color: var(--color-text);
  display: block;
  line-height: 1.5em;
  font-weight: 500;
  padding-inline-end: var(--spacing-6);
  transition: 0.3s cubic-bezier(0.64, 0.09, 0.08, 1) all;
  width: 100%;
}
.guesses__input input[type=text]:focus {
  outline: none;
}
.guesses__input label {
  font-size: 1.2rem;
  line-height: 1.3333333333;
  color: var(--color-grey);
  pointer-events: none;
  position: relative;
  transition: 0.2s ease all;
}

.hero {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: var(--spacing-2) 0;
