  gap: 0.8rem;
}
.hero__exit:is(:hover, :focus) {
  text-decoration: none;
  background-color: var(--color-grey-light);
  border-color: var(--color-grey);
}
.hero__exit img {
  block-size: 1.8rem;
  inline-size: 1.8rem;
}

.intro {
  align-items: center;
  background-color: var(--color-black-950);
  border-radius: 1.6rem;
  border: 0.6rem solid var(--color-black-950);
  margin-block-start: 7rem;
  text-align: center;
}
.intro__img {
  block-size: 23.2rem;
  inline-size: 49.6rem;
  position: relative;
  margin-block-start: -14rem;
}
.intro__content {
  padding-inline: 4.8rem;
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 6.4rem;
}
.intro__content p {
  font-size: 2.4rem;
  line-height: 1.5;
  color: var(--color-text);
  font-weight: 500;
  max-inline-size: 76.8rem;
}
.intro .loader {
  margin-block-start: -12rem;
}
.intro__container {
  align-items: center;
  background: var(--color-yellow-400);
  border-radius: 0.8rem 0.8rem 0.4rem 0.4rem;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  padding-block: 5rem 4.4rem;
  position: relative;
}
.intro__step {
  align-items: flex-start;
  background: var(--color-yellow);
  border-radius: 0.4rem;
  display: grid;
  gap: 1.4rem;
  grid-template-columns: 8rem auto;
  padding: 4rem 2.4rem;
  text-align: left;
  margin-block-start: 0.4rem;
}
.intro__step span {
  font-size: 1.9rem;
  line-height: 1.5789473684;
  font-weight: 700;
  letter-spacing: 0.05rem;
  font-family: var(--font-jura);
}
.intro__step p {
  font-size: 1.9rem;
  line-height: 1.5789473684;
  color: var(--color-text);
  font-family: var(--font-roboto);
  font-weight: 500;
  letter-spacing: 0.05rem;
}

.loader-container {
  align-items: center;
  background: var(--color-green);
  border-radius: 2rem;
  border: 0.6rem solid var(--color-black-950);
  display: flex;
  flex-direction: column;
  gap: 3.2rem;
  margin-block-start: 7rem;
  padding: 8rem;
}
.prompts-submitted .loader-container {
  background: var(--color-green);
}
.prompts-submitted .loader-container .loader {
  background: var(--color-green-light);
}
.guesses-submitted .loader-container {
  background: var(--color-blue);
}
.guesses-submitted .loader-container .loader {
  background: var(--color-blue-light);
}
.loader-container > img {
  block-size: 23.2rem;
  inline-size: 49.6rem;
  position: relative;
  margin-block-start: -16rem;
}
.loader-container .loader {
  margin-block-start: -15rem;
}

.loader {
  font-size: 1.8rem;
  line-height: 1.6666666667;
  align-items: center;
  background: var(--color-yellow-400);
  block-size: 48rem;
  border-radius: 100%;
  color: var(--color-text);
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  inline-size: 48rem;
  justify-content: center;
  letter-spacing: 0.05rem;
  padding-block: var(--spacing-4);
  text-align: center;
}
.loader img {
  block-size: 4.8rem;
  inline-size: 4.8rem;
}

.player-tag {
  font-size: 2.4rem;
  align-items: center;
  background: var(--color-yellow-400);
  border-radius: 0.8rem;
  border: 0.1rem solid var(--color-yellow-400);
  display: flex;
  font-family: var(--font-jura);
  font-weight: 700;
  gap: 0.8rem;
  letter-spacing: 0.05rem;
  padding: 0.4rem 1.6rem;
}

.prompts {
  background: var(--color-green-medium);
  border-radius: 2rem;
  border: 0.6rem solid var(--color-green-medium);
  margin-block-start: 0.4rem;
  position: relative;
  overflow: hidden;
}
.prompts.loading {
  align-items: center;
  background: var(--color-green);
  display: flex;
  flex-direction: column;
  gap: var(--spacing-4);
  padding: 8rem;
}
.prompts .loader {
  background-color: var(--color-green-light);
}
.prompts__header {
  background: var(--color-green-medium);
  border-start-end-radius: 0.6rem;
  border-start-start-radius: 0.6rem;
  color: var(--color-white);
  padding: 2.4rem 1.6rem;
}
.prompts__header span {
  font-size: 1.8rem;
  font-family: var(--font-jura);
  font-weight: 700;
  letter-spacing: 0.05rem;
}
.prompts__header span + p {
  margin-block-start: 1rem;
}
.prompts__header p {
  font-size: 1.8rem;
  color: var(--color-white);
  font-family: var(--font-jura);
  font-weight: 700;
  letter-spacing: 0.05rem;
  letter-spacing: 0.05rem;
}
.prompts__caption {
  align-items: center;
  background: var(--color-green);
  padding: 2.4rem 1.6rem;
  display: grid;
  grid-column-gap: var(--spacing-2);
  grid-template-columns: 1fr auto;
  grid-template-areas: "input submit" "note note";
  inline-size: 100%;
  text-align: center;
}
.prompts__caption + .prompts__caption {
  border-block-start: 0.1rem solid var(--color-green-light);
}
.prompts__caption:has(input[type=text][disabled]) {
  background: var(--color-white);
  pointer-events: none;
  grid-column-gap: 2rem;
}
.prompts__caption:has(input[type=text][disabled]) .prompts__caption-input {
  background: var(--color-white);
  border-color: var(--color-green-light);
}
.prompts__caption:has(input[type=text][disabled]) .prompts__caption-input button[id=keyboard] {
  display: none;
}
.prompts__caption:has(input[type=text][disabled]) .prompts__caption-submit .button {
  background: var(--color-white);
  box-shadow: none;
  border: 0.1rem solid var(--color-green-light);
  color: var(--color-green-medium);
}
.prompts__caption-submit {
  block-size: 100%;
  grid-area: submit;
}
.prompts__caption-submit .button {
  background: var(--color-yellow);
  border: 0.1rem solid var(--color-green-medium);
  color: var(--colot-text);
  block-size: 100%;
  padding-inline: var(--spacing);
}
.prompts__caption-submit .button[disabled] {
  background: var(--color-white);
  box-shadow: none;
  border: 0.1rem solid var(--color-green-medium);
  color: var(--color-green-medium);
  opacity: 0.6;
}
.prompts__caption-submit .button img {
  pointer-events: none;
}
.prompts__caption-note {
  font-size: 1.2rem;
  line-height: 1.3333333333;
  color: var(--color-white);
  font-weight: 300;
  grid-area: note;
  justify-self: flex-start;
  margin-block-start: 1.2rem;
  padding-inline-start: 2.4rem;
}
.prompts__caption-input {
  background: var(--color-white);
  border: 0.1rem solid var(--color-green-medium);
  border-radius: var(--border-radius-md);
  color: var(--color-text);
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  grid-area: input;
  overflow: hidden;
  padding: var(--spacing-2) var(--spacing-3);
  position: relative;
  text-align: left;
  transition: all var(--td) var(--tt);
}
.prompts__caption-input.active:after {
  content: "";
  position: absolute;
  bottom: 0;
  block-size: 0.4rem;
  background: var(--color-green-medium);
  inline-size: 100%;
  left: 0;
}
.prompts__caption-input button[id=keyboard] {
  background: transparent;
  border-radius: 50%;
  border: 0;
  cursor: pointer;
  position: absolute;
  right: 1.6rem;
  top: 50%;
  transform: translateY(-50%);
}
.prompts__caption-input button[id=keyboard] img {
  pointer-events: none;
}
.prompts__caption-input input[type=text] {
  font-size: 1.9rem;
  background: transparent;
  border: 0;
  color: var(--color-text);
  display: block;
  font-weight: 500;
  inline-size: 100%;
  line-height: 1.5em;
  padding-inline-end: var(--spacing-6);
}
form:not(.submitted) .prompts__caption-input input[type=text] {
  color: var(--color-green);
}
.prompts__caption-input input[type=text]:focus {
  outline: none;
}
.prompts__caption-input input[type=text]::placeholder {
  color: var(--color-green);
}
.prompts__caption-input label {
  font-size: 1.2rem;
  line-height: 1.3333333333;
  color: var(--color-grey);
  pointer-events: none;
  position: relative;
  transition: 0.2s ease all;
}

.results {
  text-align: center;
  margin-block-start: 1.6rem;
}
.results #playAgain {
  display: inline-block;
  margin-block: var(--spacing-6) 0;
  margin-inline: auto;
}
.results__container {
  display: flex;
  flex-direction: column;
  gap: 8.8rem;
}
.results__guesses {
